<template>
  <div class="bg">
    <div class="dowebok">
      <div class="logo"></div>
      <div class="form-item">
        <input id="username" @keyup.enter="login" v-model="username" type="text" autocomplete="off" placeholder="用户名">
        <p class="tip">请输入用户名</p>
      </div>
      <div class="form-item">
        <input id="password" v-model="password" @keyup.enter="login" type="password" autocomplete="off" placeholder="登录密码">
        <p class="tip">邮箱或密码不正确</p>
      </div>
      <div class="form-item"><button id="submit" @click="login">登 录</button></div>
      <!-- <div class="reg-bar">
        <a class="reg" href="javascript:">立即注册</a>
        <a class="forget" href="javascript:">忘记密码</a>
      </div> -->
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      username: "",
      password: "",
      flag: false
    }
  },
  mounted() {
  },
  methods: {
    login() {
      // sessionStorage.setItem("userName", "this.username");
      //     this.$router.push({ path: "/list" });
      //     return
      if (!this.username) {
        this.$message.warning("请输入用户名");
        return;
      }
      if (!this.password) {
        this.$message.warning("请输入密码");
        return;
      }
      let param = {
        username: this.username,
        password: this.password
      }
      this.axios({
        method: "post",
        url: "http://" + this.serviceAddr + ":8050/qs/api/v1/login",
        data: JSON.stringify(param),
        headers: {
          "Content-Type": "application/json"
        }
      }).then(response => {
        if (response.data.code == 0) {
          sessionStorage.setItem("userName", this.username);
          this.$router.push({ path: "/list" });
        } else {
          this.$message.error(response.data.msg);
        }
        this.loading = false;
      }).catch(error => {
        this.loading = false;
        this.$message.error(error);
      });
    }
  }
}
</script>
<style scoped>
html {
  height: 100%;
}
.bg {
  height: 100%;
  background: #fff url(../../static/images/backgroud.png) no-repeat;
  background-size: 100% 100%;
}
.dowebok {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 430px;
  height: 550px;
  margin: -300px 0 0 -215px;
  border: 1px solid #fff;
  border-radius: 20px;
  overflow: hidden;
}
.logo {
  width: 104px;
  height: 104px;
  margin: 50px auto 80px;
  background: url(../../static/images/login.png) 0 0 no-repeat;
}
.form-item {
  position: relative;
  width: 360px;
  margin: 0 auto;
  padding-bottom: 30px;
}
.form-item input {
  width: 288px;
  height: 48px;
  padding-left: 70px;
  border: 1px solid #fff;
  border-radius: 25px;
  font-size: 18px;
  color: #fff;
  background-color: transparent;
  outline: none;
}
.form-item button {
  width: 360px;
  height: 50px;
  border: 0;
  border-radius: 25px;
  font-size: 18px;
  color: #1f6f4a;
  outline: none;
  cursor: pointer;
  background-color: #fff;
}
#username {
  background: url(../../static/images/emil.png) 20px 14px no-repeat;
}
#password {
  background: url(../../static/images/password.png) 23px 11px no-repeat;
}
.tip {
  display: none;
  position: absolute;
  left: 20px;
  top: 52px;
  font-size: 14px;
  color: #f50;
}
.reg-bar {
  width: 360px;
  margin: 20px auto 0;
  font-size: 14px;
  overflow: hidden;
}
.reg-bar a {
  color: #fff;
  text-decoration: none;
}
.reg-bar a:hover {
  text-decoration: underline;
}
.reg-bar .reg {
  float: left;
}
.reg-bar .forget {
  float: right;
}
.dowebok ::-webkit-input-placeholder {
  font-size: 18px;
  line-height: 1.4;
  color: #fff;
}
.dowebok :-moz-placeholder {
  font-size: 18px;
  line-height: 1.4;
  color: #fff;
}
.dowebok ::-moz-placeholder {
  font-size: 18px;
  line-height: 1.4;
  color: #fff;
}
.dowebok :-ms-input-placeholder {
  font-size: 18px;
  line-height: 1.4;
  color: #fff;
}

@media screen and (max-width: 500px) {
  * {
    box-sizing: border-box;
  }
  .dowebok {
    position: static;
    width: auto;
    height: auto;
    margin: 0 30px;
    border: 0;
    border-radius: 0;
  }
  .logo {
    margin: 50px auto;
  }
  .form-item {
    width: auto;
  }
  .form-item input,
  .form-item button,
  .reg-bar {
    width: 100%;
  }
}
</style>



